.checkout{
    background-color: white;
    height: max-content;
}

.checkout__title{
    margin-right: 10px;
    padding: 15px;
    border-bottom: 1px solid lightgray;
}

.checkout__ad{
    padding-top: 55px;
    width: 100%;
    margin-bottom: 10px;
}

@media only screen and (min-width: 1000px) {
    .checkout{
        display: flex;
        flex-direction: row;
    }
  }