.product{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 60px;
    background-color: white;
    border-radius: 20px;
    z-index: 1;
}

.product__info{
    height: 100px;
    margin-bottom: 15px;
}

.product__price{
    margin-top:5px;
}

.product__rating{
    display: flex;
}

.product > img{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}

.product > button{
    max-width: 100px;
    min-width:60px;
    height: 35px;
    background-color: #f0c14b;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
    border-radius: 3px;
    cursor: pointer;
}

