.login{
    display: flex;
    background-color: white;
    height: 100vh;
    flex-direction: column;
    align-items: center;
}

.login__container{
    width: 300px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 1px solid lightgray;
    border-radius: 10px;
}

.login__container > h1{
    font-weight: 500;
    margin-bottom: 20px;
}

.login__container > form > h5{
    margin-bottom: 5px;
}

.login__container > form > input{
    margin-bottom: 10px;
    height: 30px;
    background-color: white;
    width: 98%;
    border-radius: 3px;
}

.login__container > p{
    margin-top: 20px;
    font-size: 12px;
    font-weight: 500;
    text-align: center;
}

.login__logo{
    width: 100px;
    object-fit: contain;
    margin-top: 20px;
    margin-bottom: 20px;
}

.login__signInButton {
    height: 35px;
    width: 100%;
    background-color: #f0c14b;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
    border-radius: 3px;
    cursor: pointer;
    margin-top: 10px;
}

.login__registerButton {
    height: 35px;
    width: 100%;
    background-color: #f0c14ba4;
    border: 1px solid;
    border-color: #a8873451 #9c7e316c #846a2963;
    border-radius: 3px;
    cursor: pointer;
    margin-top: 10px;
}