.header{
    background-color: #131921;
    display: flex;
    align-items: center;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 100;
}

.header__logo{
    object-fit: contain;
    width: 100px;
    margin: 18px 20px 0 20px ;
    cursor: pointer;
}

@media only screen and (max-width: 500px) {
    .header__logo{
        object-fit: contain;
        width: 70px;
        margin: 18px 20px 0 20px ;
        cursor: pointer;
    }
}

.header__nav {
    display: flex;
    justify-content: space-evenly;
}

.header__link{
    color: aliceblue;
    text-decoration: none;
}

.header__option{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
}

.header__optionLineOne{
    font-size: 10px;
}

.header__optionLineTwo{
    font-size: 13px;
    font-weight: 800;
}

.header__search{
    display: flex;
    /* take as much space as possible */
    flex: 1; 
}

.header__searchInput{
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}

.header__searchIcon{
    background-color: #cd9042;
    padding: 5px;
    height: 22px !important;
}

.header__optionBasket{
    display: flex;
    align-items: center;
}

.header__basketCount{
 margin-left: 10px;
 margin-right: 10px;
}